.banner{
    color: white;
    object-fit: contain;
    height: 448px;
}
.banner__contents{
    margin-left: 30px;
    padding-top: 140px; 
    height: 190px;
}
.banner__title{
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
}
.banner__description{
 width: 35rem;
 line-height: 1.3;
 padding-top: 1rem;
 font-size: 0.8rem;
 max-width: 360px;
 height: 80px;
}
.banner__button{
    cursor: pointer;
    color: white;
    outline: none;
    border:none;
    font-weight: 700;
    border-radius: 0.2vw;
    padding-left: 2rem;
    padding-right: 2rem;
    margin-right: 1rem;
    padding-top: .5rem;
    background-color: rgba(51,51,51,.5);
    padding-bottom: .5rem;
}
.banner__button:hover{
    color:black;
    background-color:white;
    transition: all .2s;
}
.class_fadeBottom{
    height: 7.4rem;
    background-image: linear-gradient(180deg,transparent, rgba(35.35,35,.61),#111);
}