.row{
    color:white;
    margin-left: 20px;
}
.row__posters{
    display: flex;
    overflow-y:hidden;
    overflow-x:scroll;
    padding:20px;
}
.row__posters::-webkit-scrollbar {
display: none;
}
.row__poster {
    object-fit: contain;
    width:100%;
    max-height: 100px;
    margin-right: 10px;
    transition: transform 450ms;
}
.row__poster:hover{
    transform: scale(1.08);

}
.row__PosterLarge {
  max-height: 250px;
}
.row__PosterLarge:hover{
    transform: scale(1.09);
}