.nav{
    position: fixed;
    width: 100%;
    top:0;
    height: 30px;
    padding:20px;
    z-index:1;
    display: flex;
    justify-content: space-between;
    transition-timing-function: ease-in;
    transition: all .5s;
}
.nav__black{
    background-color: #111;
}
.nav__logo{
    position: fixed;
    left: 20px;
    width: 80px;
    object-fit: contain;
}
.nav__avatar{
    position: fixed;
    right: 20px;
    width: 30px;
    object-fit: contain;
}